.page-container {
    position: relative;
    overflow-y: hidden;
    height: 93vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 20px;
    /* Primary/ Gradient */
    background: linear-gradient(180deg, #A93BFF 0%, #8834FF 100%), #DADADA;
    color: white;
}


.page-header {
    width: 100%;
    /* border: 1px solid #000000; */
    height: auto;
    display: flex;
    flex-direction: row;
    position: relative;
    justify-content: center;
}

.page-header-image {
    /* left */

    /* Auto layout */
    position: absolute;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    padding: 0px;
    left: 0;
    width: 246px;
    height: 70px;
    /* Inside auto layout */

    flex: none;
    order: 0;
    flex-grow: 0;
}

.progress-bar-container {
    display: flex;
    flex-direction: column;
}

.page-header-progress {
    display: flex;
    flex-direction: row;
    width: 60%;
    justify-content: center;
}

.page-header-progress-bar {
    width: 200px;
}

.form-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 100px 0px 0px;
    z-index: 10000;
    width: 80%;
    height: 725px;
    /* border: 1px solid #000000; */


    /* Inside auto layout */
}

.previous-page {
    position: absolute;
    width: 100%;
    height: 100%;
    padding-top: 0px;
    pointer-events: none;
    transition: transform 300ms ease 0s, opacity 300ms ease 0s;
    opacity: 0;
    transform: translateY(-100vh);
}

.current-page {
    position: absolute;
    width: 100%;
    height: 100%;
    padding-top: 0px;
    transition: transform 300ms ease 0s, opacity 200ms ease 0s;
    display: flex;
}

 

.next-page {
    position: absolute;
    width: 100%;
    height: 100%;
    padding-top: 0px;
    pointer-events: none;
    transition: transform 300ms ease 0s, opacity 200ms ease 0s;
    opacity: 0;
    transform: translateY(100vh);

}

.layout-wrapper {
    position: absolute;
    inset: 0px;
    opacity: 0;
    transition: opacity 0.2s ease 0s;
    pointer-events: none;
    z-index: -10;
}

.input-header {
    /* Auto layout */


    /* padding: 0px 248px; */
    gap: 10px;
    word-wrap: break-word;
    /*     width: 100%; */
    /* height: 50px; */


    /* Inside auto layout */

}

.input-header-text {
    /* Menu Header */
    /* height: 50px; */

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 30px;
    line-height: 50px;
    /* identical to box height, or 167% */

    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    /* White */

    color: #FFFFFF;


    /* Inside auto layout */
}

.input-header-text>p {
    margin-bottom: 0;
}

.input-caption-container {
    /* Auto layout */

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: flex-start;
    padding: 10px;
    gap: 10px;

    /*   width: 1280px; */
    height: 62px;


    /* Inside auto layout */

    flex: none;
    /*  order: 1; */
    flex-grow: 0;
}

.input-caption-text {
    width: 425px;
    height: 42px;

    /* Samplead body */

    font-family: 'Poppins';
    font-style: normal;
    font-weight: 200;
    font-size: 14px;
    line-height: 21px;
    display: flex;
    align-items: center;
    text-align: center;

    color: #FFFFFF;


    /* Inside auto layout */

    flex: none;
    order: 0;
