}

.input-box-text {
    /* Input */
    box-sizing: border-box;

    /* Auto layout */

    display: flex;
    flex-direction: row;
    align-items: flex-start;
    padding: 6px 10px;
    gap: 10px;
    width: 481px;
    height: 36px;
    left: 399.5px;
    top: 170px;
