@media (max-width: 1300px) {
    .checkbox-container {
        margin-left: 0;
        width: 100% !important;
        height: auto;
    }
    .checkbox-w-close {
        width: 100%;
    }
    
    .checkbox-col-4 {
        width: 31%;
    }

    
}

/* media query */
@media (max-width: 768px) {
    .page-container {
        min-height: 93vh;
        height: 100%;
    }
     .form-container {
        height: auto;
     }

    .checkbox-container {
        margin-left: 0;
        width: 100%;
        height: auto;
    }

    .checkbox-container-wrapper {
        /* Auto layout */
        display: flex;
        flex-direction: row;
        justify-content: center;
        width: 100%;
        height: auto;
    }

    .checkbox-input {
        width: 48%;
        margin-top: 15px;
    }

    .bottom-question-container {
        flex-direction: column;
        height: auto;
    }

    /* .radio-and-input-container {} */

    .left-question {
        margin-bottom: 80px;
    }
}
 